// Helper function to determine if current path matches given path
export const isPathActive = (pathname, path) => {
  // Handle cases where pathname or path might be undefined/null
  if (!pathname || !path) {
    return false;
  }

  // Ensure both pathname and path are strings
  const currentPath = String(pathname);
  const targetPath = String(path);

  if (targetPath === "/") {
    return currentPath === "/";
  }

  try {
    return currentPath.toLowerCase().startsWith(targetPath.toLowerCase());
  } catch (error) {
    console.error("Error in isPathActive:", error);
    return false;
  }
};

// Helper function to check if any item in a dropdown menu is active
export const isDropdownActive = (pathname, items) => {
  if (!Array.isArray(items)) {
    return false;
  }

  try {
    return items.some(
      (item) => item && item.path && isPathActive(pathname, item.path)
    );
  } catch (error) {
    console.error("Error in isDropdownActive:", error);
    return false;
  }
};
